@mixin easy-out{
  transition: all 0.5s ease-out;
}

html, body {
  width: 100%;
  height: 100%;
  background: white;
  padding: 0;
  margin: 0;
  font-family: "fira";
  box-sizing: border-box;
  border-collapse: collapse;

  @media (max-width: 960px) {
    background: #fff7fb;
  }
}

.menu-top__button{
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  z-index: 999;
  display: none;
  @media (max-width: 992px){
    display: block;
  }
}
.button-line{
  width: 30px;
  height: 3px;
  background: #ff3300;
  margin: 0 0 5px 0;
}
.button-line:nth-child(3){
  margin: 0;
}
/*
*Menu*
*/
.menu-top * {
  padding: 0;
  margin: 0;
}

.menu-top{
  @media (max-width: 992px){
    display: block;
    top: 0;
    position: absolute;
    background: #ffede3;
    min-width: 100%;
    min-height: 100%;
    z-index: 1;
    //visibility: hidden;
    //opacity: 0;
    @include easy-out;
    //transform: translateX(-100%);
  }
}
.menu-top.show{
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  //@include easy-out;
}
